<template>
  <v-row class="pa-3" >
    <v-col v-if="$store.state.profile.role==='admin'" cols="6" md="3" >
      <v-skeleton-loader
          width="100%"
          type="card"
          v-if="loading"
          height="168"
      ></v-skeleton-loader>
      <card-item :to="{name:'DashComments'}" v-else type="text-danger" icon-size="24" icon="mdi-comment-outline"  :count="data.comments_disabled" title="نظرات" desc="نظر تایید نشده" />
    </v-col>
    <v-col v-if="$store.state.profile.role==='admin'" cols="6" md="3" >
      <v-skeleton-loader
          width="100%"
          type="card"
          v-if="loading"
          height="168"
      ></v-skeleton-loader>
      <card-item :to="{name:'Users'}" v-else type="text-success" icon-size="27" icon="mdi-account-outline" title="کاربران" :count="data.users" desc="کاربر تایید شده" />
    </v-col>
    <v-col v-if="$store.state.profile.role==='admin'" cols="6" md="3" >
      <v-skeleton-loader
          width="100%"
          type="card"
          v-if="loading"
          height="168"
      ></v-skeleton-loader>
      <card-item :to="{name:'DashBlog'}"  v-else type="text-muted" icon-size="27" icon="mdi-wikipedia" title="وبلاگ" :count="parseInt(data.posts_disabled) + parseInt(data.posts_enabled)" :desc="data.posts_enabled + ' پست فعال'" />
    </v-col>
    <v-col v-if="$store.state.profile.role==='admin'" cols="6" md="3" >
      <v-skeleton-loader
          width="100%"
          type="card"
          v-if="loading"
          height="168"
      ></v-skeleton-loader>
      <card-item :to="{name:'DashProjects'}" v-else type="text-success" icon-size="27" icon="mdi-chart-donut" title="پروژه" :count="data.projects" desc="پروژه موفق" />
    </v-col>
    <v-col v-if="$store.state.profile.role==='designer'" cols="6" md="3" >
      <v-skeleton-loader
          width="100%"
          type="card"
          v-if="loading"
          height="168"
      ></v-skeleton-loader>
      <card-item :to="{name:'AllRequestsProductDesigners'}" v-else type="text-muted" icon-size="27" icon="mdi-format-align-justify" title="درخواست" :count="data.designer_product_orders" desc="درخواست برای طراحی" />
    </v-col>
    <v-col v-if="$store.state.profile.role==='referee'" cols="6" md="3" >
      <v-skeleton-loader
          width="100%"
          type="card"
          v-if="loading"
          height="168"
      ></v-skeleton-loader>
      <card-item :to="{name:'AllRequestRefrees'}" v-else type="text-muted" icon-size="27" icon="mdi-format-align-justify" title="درخواست" :count="data.referee_product_orders" desc="درخواست برای داوری" />
    </v-col>
  </v-row>
</template>

<script>
import CardItem from "@/components/Dashboard/DashMain/Card";
import axios from "axios";
export default {
  name: "DashMain",
  created() {
    this.dashboardStatistics();
  },
  components: {CardItem},
  data () {
    return {
      loading : true,
      data : {
        comments_disabled : null,
        comments_enabled : null,
        designer_product_orders : null,
        posts_disabled : null,
        posts_enabled : null,
        projects : null,
        referee_product_orders : null,
        users : null,
      }
    }
  },
  methods : {
    dashboardStatistics(){
      this.loading = true;
      axios.get(this.$store.state.host + '/website/api/v1/website/dashboard-statistics',{
        headers : {
          Authorization: `Bearer ${this.$store.state.userId}`,
        }
      }).then(response=>{
        this.data = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading = false;
      })
    }
  }
}
</script>

